.container {
  height: calc(100vh - 60px);
  width: 100%;
  margin-top: 15rem;
}

.wrapper {
  height: 100%;
  width: 85%;
  margin: 0 auto;
}

.titles {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1.5rem;
  margin-bottom: 3rem;
}

.titles > h2 {
  font-size: 32px;
  color: #3507b4;
  letter-spacing: 1px;
}

.categories {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 3rem;
}

.category {
  padding: 0.25rem 0.75rem;
  border: 1px solid #0e0801;
  background-color: #fff;
  color: #2e30a8;
  border-radius: 8px;
}

.category.active {
  background-color: #d79734;
  color: #fff;
}

.recipes {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 3rem;
}

.recipe {
  height: 350px;
  width: 100%;
  display: flex;
  flex-direction: column;
  text-align: center;
  gap: 0.5rem;
}

.imgContainer {
  height: 80%;
  width: 100%;
  overflow: hidden;
  transition: 150ms;
}

.imgContainer:hover {
  transform: scale(1.025);
}

.imgContainer > img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  border-radius: 8px;
}

.recipe > h3 {
  color: #333;
}
